<template>
  <div>
    <h3 style="position auto">
      欢迎你
      {{this.$store.state.nickname}}
    </h3>

    <div
      class="demo-image__placeholder">
      <div class="block">
        <span
          class="demonstration"></span>
        <el-image :src="src"
          style="width: 35rem,; height: 35rem"
          :fit="this.fit">
          <div
            slot="placeholder"
            class="image-slot">
            加载中<span
              class="dot">...</span>
          </div>
          <div slot="error"
            class="image-slot">
            <i
              class="el-icon-picture-outline"></i>
          </div>
        </el-image>
      </div>

    </div>
    <p>
      {{data.sentence}}
    </p>
    <p
      style="position:absolute;right: 70rem">
      ------{{data.author}}
    </p>

    
    <el-button round id="btn3"
      @click="startAddGroup">
      开始录入句子或短语
    </el-button>
    <el-button round id="btn1"
      @click="startAdd">开始录入单词
    </el-button>

    <el-button type="primary"
      @click="startRecite"
      round id="btn2">开始背今日内容
    </el-button>

  </div>
</template>

<script>
import axios from 'axios'

export default {
  components: {},
  methods: {
    /**
     * 先请求单词数据并携带数据再做跳转
     */
    startRecite() {
      axios({
        method: 'post',
        url: this.$baseURL+'/word/todayWords',
        data: {
          uid: this.$store.state.uid,
        },
      }).then((res) => {
        this.$router.push({
        path: '/pc/reciteWords',
        query: { data:res.data.data },
      })
      console.log(res.data.data)
        if (
          res.data.data.length == 0 &&
          !(
            this.$store.state.uid == null ||
            this.$store.state.uid == '' ||
            this.$store.state.uid === undefined
          )
        ) {
          alert(
            '无数据,请先去添加'
          )
        }
      })  
    },


    startAdd() {
      this.$router.push('/pc/addWords')
    },
    startAddGroup() {
      this.$router.push('/pc/addGroup')
    },

  },
  name: 'MyIndex',
  data() {
    return {
      data: '',
      src: '',
      fit: 'contain',
    }
  },
  mounted() {
      // let info =navigator.userAgent
      // if(/mobile/i.test(info)){
      //   window.location.href='http://www.baidu.com'

      // }

    if (
      this.$store.state.uid == null ||
      this.$store.state.uid == '' ||
      this.$store.state.uid === undefined
    ) {
      this.$router.push('/pc/login')
    }

    axios({
      method: 'post',
      url: this.$baseURL+'/getOne',
      data: {},
    }).then((res) => {
      this.data = res.data.data
      this.src = this.data.pic
    })
  },
}
</script>
<style scoped>
/* 句子短语按钮 */
#btn3 {
  position: absolute;
  left: 5.6rem;
  bottom: 2.5rem;
}
/* 录入单词按钮 */
#btn1 {
  position: absolute;
  left: 5rem;
  bottom: 6.25rem;
}
/* 背单词按钮 */
#btn2 {
  position: absolute;
  right: 5rem;
  bottom: 2.5rem;
}
.el-button{
  width: 300px;
  height: 50px;
}
</style>